@import url(https://fonts.googleapis.com/css2?family=Sarala&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Sarala&display=swap);
.landing__title {
  font-family: "Sarala";
  font-style: normal;
  font-weight: bold;
  font-size: 12rem;
  letter-spacing: 0.12em;
  display: flex;
  flex-flow: wrap-reverse;
  color: #444444;
}
.landing__subtitle {
  font-family: "Sarala";
  font-style: normal;
  font-weight: bold;
  font-size: 3rem;
  letter-spacing: 0.12em;
  text-transform: uppercase;
  color: #fc9685;
}
.landing__user-info {
  font-family: "Sarala";
  font-style: normal;
  font-weight: bold;
  font-size: 2.9rem;
  letter-spacing: 0.03em;
  margin-top: 4rem;
}
.landing__user-info--paragraph {
  font-size: 1.5rem;
}

.portfolio {
  max-width: 120rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
@media only screen and (max-width: 90em) {
  .portfolio {
    margin: 0;
    max-width: 100%;
    width: 100%;
  }
}
.portfolio__content {
  height: 100vh;
  position: relative;
  background: pink;
  z-index: 400;
}

.content__landing {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden !important;
  background-color: #fbfcff;
}
.content__about {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden !important;
  height: 30%;
  background-color: #444444;
}
.content__contact {
  display: flex;
  position: relative;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  height: calc(1vh * 100);
  height: calc(var(--vh, 1vh) * 100);
  overflow: hidden !important;
  background-color: #fbfcff;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  box-sizing: border-box;
  font-size: 62.5%;
}
@media only screen and (max-width: 75em) {
  html {
    font-size: 50%;
  }
}

body {
  height: 100%;
  line-height: 1.6;
  min-height: 100vh;
}

a {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 200 !important;
}

